
.social-links-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 35vw;
    margin: 10vh auto;
}

.circle {
    font-size: 8vw;
    height: 1em;
    width: 1em;
    background-color: #a30000;
    position: relative;
    overflow: hidden;
    /* box-shadow: inset 0.5em 1em #000000; */
}

/* INSTAGRAM LOGO */

.insta {
    aspect-ratio: 1;
    border-radius: 50%;
    display: grid;
    place-items: center;
}

.insta::before {
    content: '';
    color: #000000;
    position: absolute;
    /* border-radius: 0.2em; */
    border-radius: 30%;
    aspect-ratio: inherit;
    border: 0.07em solid;
    width: 55%;
    height: 55%;
}

.insta::after {
    content: '';
    color: #000000;
    position: absolute;
    border-radius: 50%;
    aspect-ratio: 1;
    border: 0.07em solid;
    width: 20%;
    box-shadow: 0.18em -0.18em 0 -0.13em;
}

/* FACEBOOK LOGO */

.facebook {
    border: .125em solid #a30000;
    border-bottom: 0;
    border-right: .275em solid #a30000;
    clip-path: ellipse(50% 50% at 50% 50%);
    box-sizing: border-box;
    /* outline: transparent; */
}

.facebook::before {
    content: '';
    position: absolute;
    background-color: inherit;
    width: .4em;
    height: 1em;
    top: .17em;
    right: -.25em;
    border: solid #000000;
    border-width: .04em 0 0 .058em;
    /* border-radius: .1em; */
    border-radius: .125em / .1em;
    box-shadow: 0 0 0 .1em #000000;
    clip-path: ellipse(150% 130% at 25% 120%);
}

.facebook::after {
    content: '';
    position: absolute;
    background: #000000;
    top: .38em;
    right: .01em;
    width: .425em;
    height: .14em;
    clip-path: polygon(0% 0%, 100% 0%, 95% 100%, 0% 100%);
}

/* LINKEDIN LOGO */

.linkedin {
    border: solid #a30000;
    border-width: 0 .2em .2em 0;
    box-sizing: border-box;
    clip-path: ellipse(50% 50% at 50% 50%);
}

.linkedin::after {
    content: '';
    position: absolute;
    background-color: #a30000;
    width: .08em;
    height: .4em;
    border: .125em solid #000000;
    color: #000000;
    border-top: 0;
    border-bottom: 0;
    top: .4em;
    left: .22em;
    box-shadow: 0.41em 0.07em 0 -0.05em;
}

.linkedin::before {
    content: '';
    position: absolute;
    background-color: #a30000; /* aa2e2e */
    width: .27em;
    height: .27em;
    right: .00em;
    top: .4em;
    border-radius: 50%;
    box-shadow: -.385em -.26em 0 -.06em #000000, inset -.053em .05em 0 .069em #000000;
    clip-path: polygon(-150% -100%, 100% -100%, 100% 65%, -150% 85%);
}

.ct-form {
    display: flex;
    flex-direction: column;
    width: 50%;
    margin: 2rem auto;
}

.ct-form input, textarea {
    border: none;
    border-bottom: 2px solid black;
    margin: .5rem 0;
    font: inherit;
}

.sub-btn {
    width: 30%;
    margin: 1.5rem auto;
    font: icon;
    color: white;
    background-color: black;
    border: none;
    padding: 1rem;
    /* padding: .3rem; */
    overflow: hidden;
    position: relative;
    transition: all .3s ease;
    transform: skewX(-20deg);
    z-index: 10;
}

.sub-btn::before {
    content: "";
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    transform: scaleX(0);
    transition: .3s all ease;
    width: 100%;
    z-index: -1;
    background-color: #a30000;
}

.sub-btn:hover {
    color: black;
    cursor: pointer;
}

.sub-btn:hover::before {
    transform: scaleX(1);
}

@media screen and (max-width: 768px){
    .social-links-container {
        width: 75vw;
    }
    
    .circle {
        font-size: 20vw;
    }

    .ct-form {
        width: 90%;
    }

    .sub-btn {
        width: 50%;
        font-size: larger;
    }
}